import React, {useEffect, useMemo} from 'react'
import {useParams,useLocation} from 'react-router-dom'
import styled from 'styled-components'
import {useWallet} from 'use-wallet'
import {provider} from 'web3-core'
import PageHeader from '../../components/PageHeader'
import Spacer from '../../components/Spacer'
import useFarm from '../../hooks/useFarm'
import {getContract} from '../../utils/erc20'
import Harvest from './components/Harvest'
import Stake from './components/Stake'

import bg1 from '../../assets/img/bg1.png'
import details_logo from '../../assets/img/details-logo.png'

const Farm: React.FC = () => {
    const {farmId} = useParams()
    const {
        poolContract,
        lpToken,
        lpTokenAddress,
        day,
    } = useFarm(farmId) || {
        pid: 0,
        lpToken: '',
        poolAddress: '',
        lpTokenAddress: '',
        tokenAddress: '',
        earnToken: '',
        name: '',
        icon: '',
        day: 0,
    }

    useEffect(() => {
        window.scrollTo(0, 0)
    }, [])

    const {ethereum} = useWallet()

    const lpContract = useMemo(() => {
        return getContract(ethereum as provider, lpTokenAddress)
    }, [ethereum, lpTokenAddress])

    return (
        <StyledContainerWrapper>
            <PageHeader
                icon={<img src={details_logo} height="120" alt="details logo"/>}
                title="POOL"
                subtitle={<HeaderDesc>Deposit PTT MASTER Tokens and earn PTT.<br/>Penalty of 90% if harvest
                    within {day} days</HeaderDesc>}
            />
            <StyledFarm>
                <StyledCardsWrapper>
                    <StyledCardWrapper>
                        <Harvest contract={poolContract}/>
                    </StyledCardWrapper>
                    <Spacer/>
                    <StyledCardWrapper>
                        {poolContract && <Stake
                            lpContract={lpContract}
                            contract={poolContract}
                            tokenName={lpToken.toUpperCase()}
                        />}
                    </StyledCardWrapper>
                </StyledCardsWrapper>
                <Spacer size="lg"/>
                <div style={{textAlign: 'center'}}>
                    <a href="https://anyswap.exchange/add-liquidity"
                       style={{color: 'white', textDecoration: 'none', borderBottom: '1px solid white'}}
                       target="_blank">Provide liqudity to PTT-HT pair on ANYSWAP</a>
                </div>
                <Spacer size="lg"/>
            </StyledFarm>
        </StyledContainerWrapper>
    )
}

const StyledContainerWrapper = styled.div`
  background-image: url('${bg1}');
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  background-position-y: bottom;
  min-height: calc(100vh - 279px);
  margin-top: -1px;

  & > div:first-child {
    margin-top: 50px;
  }

  @media (max-width: 1024px) {
    background-size: cover;
  }

  @media (max-width: 768px) {
    background-size: cover;
  }

  @media (max-width: 576px) {
    background-size: cover;
  }
`

const StyledFarm = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 100%;
  }
`

const StyledCardsWrapper = styled.div`
  display: flex;
  width: 600px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  margin: 0 20px;
  @media (max-width: 768px) {
    width: 80%;
  }
`
const HeaderDesc = styled.div`
  text-align: center;
`

export default Farm
