import React from 'react'
import styled from 'styled-components'

import logo from '../../assets/img/logo.png'
import twitter from '../../assets/img/twitter.png'
import telegram from '../../assets/img/telegram.png'
import medium from '../../assets/img/medium.png'

import {useLocation} from 'react-router-dom'
import Nav from './components/Nav'

const Footer: React.FC = () => {
    const location = useLocation();
    const isHomePage = location.pathname === '/';

    return (
        <StyledFooter>
            {
                isHomePage &&
                <StyledFooterInner>
                    <StyledImage>
                        <img src={logo}/>
                    </StyledImage>
                    <div>
                        <StyledFooterTitle>About us</StyledFooterTitle>
                        <Nav/>
                    </div>
                    <div>
                        <StyledFooterTitle>Community</StyledFooterTitle>
                        <StyledSocials>
                            <a href="https://twitter.com/OpenFiLand" target="_blank"><img src={twitter}/></a>
                            <a href="https://t.me/open_finance_en" target="_blank"><img src={telegram}/></a>
                            <a href="https://medium.com/@OpenFiLand" target="_blank"><img src={medium}/></a>
                        </StyledSocials>
                    </div>
                </StyledFooterInner>
            }

            {
                <StyledSocialsWrapper>
                    <StyledSocials>
                        <a href="https://twitter.com/OpenFiLand" target="_blank"><img src={twitter}/></a>
                        <a href="https://t.me/open_finance_en" target="_blank"><img src={telegram}/></a>
                        <a href="https://medium.com/@OpenFiLand" target="_blank"><img src={medium}/></a>
                    </StyledSocials>
                </StyledSocialsWrapper>
            }
            <div> 2021 Open.Finance</div>
        </StyledFooter>
    )
}

const StyledFooter = styled.footer`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: ${(props) => props.theme.color.primary.main};
    color: ${(props) => props.theme.color.white};
    
    padding: 30px 0;
`

const StyledFooterInner = styled.div`
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    max-width: ${props => props.theme.siteWidth}px;
    width: 100%;
    margin-bottom: 50px;
    
    @media (max-width: 576px) {
        width: 90%;
        margin: 0 auto;
    }
`

const StyledImage = styled.div`
    & > img {
        width: 80%;
    }
    
    @media (max-width: 576px) {
        margin-bottom: 20px;
    }
`

const StyledFooterTitle = styled.div`
    color: #fff;
    font-weight: bold;
    font-family: 'GothamVnu-Bold';
    font-size: 22px;
`

const StyledSocials = styled.div`
    margin-top: 15px;
    
    & > a {
        margin-right: 15px;
    }
    
    & img {
        width: 30px;
    }
`

const StyledSocialsWrapper = styled.div`
    & > div {
        margin: 0;
        margin-bottom: 20px;
        padding: 4px;
    }
    
    & > div > a {
        margin: 0 20px;
    }
`

export default Footer