import React, {useEffect, useState} from 'react'
import styled from "styled-components"
import Container from "../../../components/Container"
import bg4 from '../../../assets/img/bg4.jpg'
import logo from '../../../assets/img/logo.png'

const Blog: React.FC = () => {
    const [news, setNews] = useState([]);

    function getWords(value) {
        if (!value) {
            return value;
        }

        const sentence = value.replace(/(<([^>]+)>)/ig, '').substring(0, 400);
        const lastIndexOf = sentence.lastIndexOf(' ');

        return sentence.substring(0, lastIndexOf) + ' ...';
    }

    useEffect(() => {
        fetch('https://api.rss2json.com/v1/api.json?rss_url=https://medium.com/feed/@openfiland&api_key=90pvezoevafgfptadpqfurlp4vdgbznglauavive&count=3')
            .then(response => response.json())
            .then(data => {
                console.log(data)
                let results = [];
                // @ts-ignore
                if (data && data.items) {
                    // @ts-ignore
                    for (let index = 0; index < data.items.length; index++) {
                        // @ts-ignore
                        let item = data.items[index]
                        results.push({
                            title: item.title,
                            description: item.description,
                            thumbnail: item.thumbnail,
                            link: item.link,
                            pubDate: item.pubDate
                        })
                    }

                    setNews([...results]);
                }
            });

    }, [])

    return (
        <StyledWrapper id="blog">
            <Container size="lg">
                <StyledTitle className="title">BLOG</StyledTitle>
                <StyledItems className="content-wrapper">
                    {
                        news.length > 0 && news.map((item, index) => {
                            return (
                                <StyledItem key={index}>
                                    <a href={item.link} target="_blank">
                                        <StyledItemImage>
                                            <img src={item.thumbnail}/>
                                        </StyledItemImage>
                                    </a>
                                    <StyledItemContent>
                                        <StyledItemTitle>
                                            <a href={item.link} target="_blank">
                                                {item.title}
                                            </a>
                                        </StyledItemTitle>
                                        <StyledItemDate>
                                            {item.pubDate}
                                        </StyledItemDate>
                                        <StyledItemDescription>
                                            {getWords(item.description)}
                                        </StyledItemDescription>
                                        <StyledItemReadmore>
                                            <a href={item.link} target="_blank">Read more</a>
                                        </StyledItemReadmore>
                                    </StyledItemContent>
                                </StyledItem>
                            )
                        })
                    }

                </StyledItems>

                <StyleWrapperButton>
                    <a href="https://medium.com/@openfiland" target="_blank">
                        <StyledButton>LEARN MORE</StyledButton>
                    </a>
                </StyleWrapperButton>
            </Container>
        </StyledWrapper>
    )
}

const StyledWrapper = styled.div`
    background-image: url('${bg4}');
    background-repeat: no-repeat;
    width: 100%;
    padding: 50px 0;
    background-size: cover;
`

const StyledTitle = styled.div`
                    `

const StyledItems = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 20px;
`

const StyledItem = styled.div`
    display: flex;
    margin-top: 20px;
    
    @media (max-width: 576px) {
        flex-direction: column;
    }
`

const StyledItemImage = styled.div`
    overflow: hidden;
    width: 300px;
    height: 200px;
    
    background-image: url('${logo}');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    background-color: #22485D;
    
    & > img {
        height: 100%;
        width: 100%;
    }
    
    @media (max-width: 576px) {
        width: 100%;
        margin-top: 20px;
    }
`

const StyledItemContent = styled.div`
    margin-left: 30px;
    
    @media (max-width: 576px) {
        margin-left: 0;
    }
`

const StyledItemTitle = styled.div`
    & > a {
        color: ${(props) => props.theme.color.primary.main};
        font-weight: bold;
        font-family: 'GothamVnu-Bold';
        font-size: 20px;
        text-transform: uppercase;
        text-decoration: none;
        
        @media (max-width: 576px) {
            .title {
                font-size: 16px;
            }
        }
        
        &:hover {
            opacity: .8;
        }
    }
`

const StyledItemDate = styled.div`
    font-size: 12px;
    margin: 20px 0;
`

const StyledItemDescription = styled.div`
    font-size: 14px;
`

const StyledItemReadmore = styled.div`
    margin-top: 30px;
    font-size: 12px;
    
    & > a {
        color: ${(props) => props.theme.color.primary.light};
        text-decoration: none;
    }
    
    & > a:hover {
        opacity: .8;
    }
`

const StyleWrapperButton = styled.div`
    text-align: center;
    
    & > a:hover {
        opacity: .8;
    }
`

const StyledButton = styled.button`
    width: auto;
    margin-top: 30px;
    padding: 15px 20px;
    font-weight: bold;
    font-size: 16px;
    background: none;
    color: ${(props) => props.theme.color.primary.light};
    border: 1px solid ${(props) => props.theme.color.primary.light};
    border-radius: 0;
    box-shadow: none;
    cursor: pointer;
    
    &:hover {
        color: ${(props) => props.theme.color.primary.main};
    }
    
    &:focus {
        outline: none;
    }
`

export default Blog
