import React, {useState} from 'react'
import styled from 'styled-components'
import Card from '../../../components/Card'
import CardContent from '../../../components/CardContent'
import CardIcon from '../../../components/CardIcon'
import Label from '../../../components/Label'
import Value from '../../../components/Value'
import useEarnings from '../../../hooks/useEarnings'
import useReward from '../../../hooks/useReward'
import {getBalanceNumber} from '../../../utils/formatBalance'
import details_logo from '../../../assets/img/details-logo.png'
import {Contract} from "web3-eth-contract";

interface HarvestProps {
    contract: Contract
}

const Harvest: React.FC<HarvestProps> = ({contract}) => {
    const earnings = useEarnings(contract)
    const [pendingTx, setPendingTx] = useState(false)
    const {onReward} = useReward(contract)

    return (
        <Card>
            <CardContent>
                <StyledCardContentInner>
                    <StyledCardHeader>
                        <CardIcon><img src={details_logo}/></CardIcon>
                        <Value value={getBalanceNumber(earnings)} decimals={8}/>
                        <Label text="$PTT Earned"/>
                    </StyledCardHeader>
                    <StyledCardActions>
                        <StyledButton
                            disabled={!earnings.toNumber() || pendingTx}
                            onClick={async () => {
                                setPendingTx(true)
                                await onReward()
                                setPendingTx(false)
                            }}>
                            {pendingTx ? 'Collecting $PTT' : 'Harvest'}
                        </StyledButton>
                    </StyledCardActions>
                </StyledCardContentInner>
            </CardContent>
        </Card>
    )
}

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;

  & > div:first-child {
    margin: 20px 0;
  }

  & > div:nth-child(2) {
    margin: 10px 0;
  }
`
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`

const StyledSpacer = styled.div`
  height: ${(props) => props.theme.spacing[4]}px;
  width: ${(props) => props.theme.spacing[4]}px;
`

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`

const StyledButton = styled.button`
  width: 100%;
  height: 40px;
  padding: 0 10px;
  font-weight: bold;
  font-size: 16px;
  background: none;
  color: white;
  border: 1px solid white;
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    border-color: ${(props) => props.theme.color.green[800]};
    color: ${(props) => props.theme.color.green[800]};
  }

  &:focus {
    outline: none;
  }
`

export default Harvest
