import React, {useState} from 'react'
import styled from "styled-components"
import Container from "../../../components/Container"
import {useTranslation} from "react-i18next"

const StayInTouch: React.FC = () => {
    const {t} = useTranslation()

    const [submitting, setSubmitting] = useState(false)
    const [errorMessage, setErrorMessage] = useState("")
    const [successMessage, setSuccessMessage] = useState("")
    const [email, setEmail] = useState("")

    const onSubscribe = async () => {
        setErrorMessage("")
        setSuccessMessage("")

        if (!email || !(/^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(email))) {
            setErrorMessage(t("home.stayInTouch.subscribeError"))

            return;
        }

        setSubmitting(true)

        const postUrl = "https://finance.us7.list-manage.com/subscribe/post?u=4dfefd2a55e5f85225667750d&amp;id=dd489d72d2"

        const formData = new FormData()
        formData.append("EMAIL", email)

        await fetch(postUrl, {
            method: "POST",
            body: formData,
            mode: 'no-cors'
        }).then(function (response) {
            setSubmitting(false)
            setSuccessMessage(t("home.stayInTouch.subscribeSuccess"))

            setEmail("")

            setTimeout(() => {
                setSuccessMessage("")
            }, 4000)
        }).catch(function (error) {
            setSubmitting(false)
            setErrorMessage(t("home.stayInTouch.subscribeError"))
        })
    }

    return (
        <StyledWrapper>
            <Container size="lg">
                <StyledTitle className="title">{t("home.stayInTouch.title")}</StyledTitle>
                <StyledForm className="content-wrapper">
                    <StyledControl>
                        <input type="text" placeholder={t("home.stayInTouch.emailPlaceholder")} value={email}
                               onChange={e => setEmail(e.target.value)}/>
                        <button onClick={onSubscribe} disabled={submitting}>{t("home.stayInTouch.submit")}</button>
                        {submitting && <span className="loader"></span>}
                    </StyledControl>
                    {errorMessage && <StyledMessage>{errorMessage}</StyledMessage>}
                    {successMessage && <StyledSuccessMessage>{successMessage}</StyledSuccessMessage>}
                </StyledForm>
            </Container>
        </StyledWrapper>
    )
}

const StyledWrapper = styled.div`
    width: 100%;
    padding: 50px 0;
    text-align: center;
    background-color: #fff;
`

const StyledTitle = styled.div`
`

const StyledForm = styled.div`
    margin-top: 20px;
`

const StyledControl = styled.div`
    width: 100%;
    display: flex;
    flex-wrap;
    justify-content: center;
    
    input {
        background-color: #F7F7F7;
        padding: 12px 100px 12px 10px;
        border: none;
        max-width: 100%;
    }
    
    input:focus {
        outline: none;
    }
    
    button {
        padding: 12px 20px;
        border: none;
        background-color: ${(props) => props.theme.color.primary.light};
        color: #fff;
        cursor: pointer;
        outline: none;
    }
    
    button:hover {
        color: ${(props) => props.theme.color.primary.main};
    }
    
    button:active, button:focus {
        outline: none;
    }
    
    @media (max-width: 768px) {
    }
    
    @media (max-width: 576px) {
        
    }
`

const StyledMessage = styled.div`
    font-size: 13px;
    font-weight: bold;
    color: #bb2027;
    min-height: 20px;
    margin: 10px;
`

const StyledSuccessMessage = styled.div`
    font-size: 13px;
    font-weight: bold;
    color: #e69e02;
    margin: 10px;
    min-height: 20px;
`

const StyledLoading = styled.div`
 
`

export default StayInTouch
