import {useCallback, useEffect, useState} from 'react'
import {provider} from 'web3-core'

import BigNumber from 'bignumber.js'
import {useWallet} from 'use-wallet'

import {getEarned, getMasterChefContract, openFi} from '../sushi/utils'
import useSushi from './useSushi'
import useBlock from './useBlock'
import {Contract} from "web3-eth-contract";


const useEarnings = (contract: Contract) => {
    const [balance, setBalance] = useState(new BigNumber(0))
    const {
        account,
    }: { account: string; ethereum: provider } = useWallet()
    const sushi = useSushi()
    const block = useBlock()

    const fetchBalance = useCallback(async () => {
        const balance = await openFi.getEarned(contract, account)
        setBalance(new BigNumber(balance))
    }, [account, contract, sushi, contract])

    useEffect(() => {
        if (account && contract && sushi) {
            fetchBalance()
        }
    }, [account, block, setBalance, sushi])

    return balance
}

export default useEarnings
