import React, {useEffect} from 'react'
import styled from 'styled-components'
import {useTranslation} from 'react-i18next'
import {Link} from 'react-router-dom'
import * as Scroll from 'react-scroll';
import {generateLink} from '../../../utils'
import {useLocation} from "react-router-dom";

const Nav: React.FC = () => {
    const {t, i18n} = useTranslation()
    const location = useLocation();
    const search = location.search
    const isHomePage = location.pathname === generateLink(i18n, '/')
    let ScrollLink = Scroll.Link;

    useEffect(() => {
        const hash = location.hash
        if (hash) {
            window.setTimeout(function () {
                let element: HTMLElement = document.querySelector(`a[href="${hash}"]`)
                if (element) {
                    element.click();
                }
            }, 100);
        }
    })

    return (
        <StyledNav>
            <span className="has-sub-menu">
                {t("menu.aboutOpenFinance")}
                <span className="arrow-down"></span>

                <ul className="sub-menu">
                    <li>
                        {
                            isHomePage &&
                            <ScrollLink href="#what-is-open-finance" to="what-is-open-finance" spy={true} smooth={true}>
                                What is Open Finance?
                            </ScrollLink>
                        }
                        {
                            !isHomePage &&
                            <Link to={{pathname: generateLink(i18n, '/'), search,hash:'#what-is-open-finance'}}>
                                What is Open Finance?
                            </Link>
                        }
                    </li>
                    <li>
                        {
                            isHomePage &&
                            <ScrollLink href="#roadmap" to="roadmap" spy={true} smooth={true}>
                                Roadmap
                            </ScrollLink>
                        }
                        {
                            !isHomePage &&
                            <Link to={{pathname: generateLink(i18n, '/'), search,hash:'#roadmap'}}>
                                Roadmap
                            </Link>
                        }
                    </li>
                </ul>
            </span>
            {
                isHomePage &&
                <ScrollLink href="#potential-of-defi" to="potential-of-defi" spy={true} smooth={true}>
                    {t("menu.potentialOfDefi")}
                </ScrollLink>
            }
            {
                !isHomePage &&
                <Link to={{pathname: generateLink(i18n, '/'), search,hash:'#potential-of-defi'}}>
                    {t("menu.potentialOfDefi")}
                </Link>
            }
            {
                isHomePage &&
                <ScrollLink href="#blog" to="blog" spy={true} smooth={true}>
                    {t('menu.blog')}
                </ScrollLink>
            }
            {
                !isHomePage &&
                <Link to={{pathname: generateLink(i18n, '/'), search, hash:'#blog'}}>
                    {t('menu.blog')}
                </Link>
            }
        </StyledNav>
    )
}

const StyledNav = styled.nav`
  align-items: center;
  display: flex;

  & > a, & > span {
    color: ${(props) => props.theme.color.white};
    font-weight: 700;
    padding: 20px;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: ${(props) => props.theme.color.primary.light};
    }

    &.active {
      color: ${(props) => props.theme.color.primary.light};
    }

    @media (max-width: 400px) {
      padding-left: ${(props) => props.theme.spacing[2]}px;
      padding-right: ${(props) => props.theme.spacing[2]}px;
    }
  }
`

export default Nav
