import React from 'react'
import styled, {keyframes} from 'styled-components'
import Card from '../../../components/Card'
import CardContent from '../../../components/CardContent'
import CardIcon from '../../../components/CardIcon'
import Loader from '../../../components/Loader'
import Spacer from '../../../components/Spacer'
import {Farm} from '../../../contexts/Farms'
import useAllStakedValue, {
    StakedValue,
} from '../../../hooks/useAllStakedValue'
import useFarms from '../../../hooks/useFarms'
import {Link, useLocation} from "react-router-dom";
import details_logo from '../../../assets/img/details-logo.png'
import details1 from '../../../assets/img/details1.png'
import {generateLink} from '../../../utils'
import {useTranslation} from 'react-i18next'

interface FarmWithStakedValue extends Farm, StakedValue {
    apy: number
}

const FarmCards: React.FC = () => {
    const [farms] = useFarms()
    const stakedValue = useAllStakedValue()

    const rows = farms.reduce<FarmWithStakedValue[][]>(
        (farmRows, farm, i) => {
            const farmWithStakedValue = {
                ...farm,
                ...stakedValue[i],
                apy: farm.apy,
            }
            const newFarmRows = [...farmRows]
            if (newFarmRows[newFarmRows.length - 1].length === 3) {
                newFarmRows.push([farmWithStakedValue])
            } else {
                newFarmRows[newFarmRows.length - 1].push(farmWithStakedValue)
            }
            return newFarmRows
        },
        [[]],
    )

    return (
        <StyledCards>
            {rows[0].length ? (
                rows.map((farmRow, i) => (
                    <StyledRow key={i}>
                        {farmRow.map((farm, j) => (
                            <React.Fragment key={j}>
                                <FarmCard farm={farm}/>
                            </React.Fragment>
                        ))}
                    </StyledRow>
                ))
            ) : (
                <StyledLoadingWrapper>
                    <Loader text="Cooking the rice ..."/>
                </StyledLoadingWrapper>
            )}
        </StyledCards>
    )
}

interface FarmCardProps {
    farm: FarmWithStakedValue
}

const FarmCard: React.FC<FarmCardProps> = ({farm}) => {
    const poolActive = true // startTime * 1000 - Date.now() <= 0
    const {t, i18n} = useTranslation()
    const {search} = useLocation()

    return (
        <StyledCardWrapper>
            {farm.tokenSymbol === 'SUSHI' && <StyledCardAccent/>}
            <Card>
                <CardContent>
                    <StyledContent>
                        <CardIcon>
                            {
                                farm.name === 'NFT Master' &&
                                <img src={details1} alt=""/>
                            }
                            {
                                farm.name !== 'NFT Master' &&
                                <img src={details_logo} alt=""/>
                            }
                        </CardIcon>
                        <StyledTitle>{farm.name}</StyledTitle>
                        <StyledDetails>
                            <StyledDetail>
                                <div>APY:</div>
                                <div>{farm.apy}%</div>
                            </StyledDetail>
                            <StyledDetail>
                                <div>Deposit:</div>
                                <div>{farm.lpToken.toUpperCase()}</div>
                            </StyledDetail>
                            <StyledDetail>
                                <div>Earn:</div>
                                <div>{farm.earnToken.toUpperCase()}</div>
                            </StyledDetail>
                            <StyledDetail>
                                <div>Days:</div>
                                <div>{farm.day}</div>
                            </StyledDetail>
                        </StyledDetails>
                        <Spacer/>

                        <StyledButton>
                            <StyledLink to={{pathname: generateLink(i18n, `/farms/${farm.id}`), search}}>{poolActive ? 'Select' : undefined}</StyledLink>
                        </StyledButton>
                        <StyledDetails>
                            <Small>Penalty of 90% if harvest within {farm.day} days</Small>
                        </StyledDetails>
                    </StyledContent>
                </CardContent>
            </Card>
        </StyledCardWrapper>
    )
}

