import React, {useCallback} from 'react'
import styled from 'styled-components'
import {useWallet} from 'use-wallet'
import useTokenBalance from '../../../hooks/useTokenBalance'
import useSushi from '../../../hooks/useSushi'
import {getSushiAddress} from '../../../sushi/utils'
import {getBalanceNumber} from '../../../utils/formatBalance'
import CardIcon from '../../CardIcon'
import Label from '../../Label'
import Modal, {ModalProps} from '../../Modal'
import ModalActions from '../../ModalActions'
import ModalContent from '../../ModalContent'
import ModalTitle from '../../ModalTitle'
import Spacer from '../../Spacer'
import Value from '../../Value'
import details_logo from "../../../assets/img/details-logo.png";
import {NavLink, BrowserRouter} from "react-router-dom";

const AccountModal: React.FC<ModalProps> = ({onDismiss}) => {
    const {account, reset} = useWallet()

    const handleSignOutClick = useCallback(() => {
        onDismiss!()
        reset()
    }, [onDismiss, reset])

    const sushi = useSushi()
    const sushiBalance = useTokenBalance(getSushiAddress(sushi))

    return (
        <Modal>
            <ModalTitle text="My Account"/>
            <ModalContent>
                <Spacer/>

                <div style={{display: 'flex'}}>
                    <StyledBalanceWrapper>
                        <CardIcon>
                            <img src={details_logo}/>
                        </CardIcon>
                        <StyledBalance>
                            <Value value={getBalanceNumber(sushiBalance)} decimals={8}/>
                            <Label text="PTT Balance"/>
                        </StyledBalance>
                    </StyledBalanceWrapper>
                </div>

                <Spacer/>
                <BrowserRouter>
                    <StyledLink to={`https://hecoinfo.com/address/${account}`}>View on Blockchain</StyledLink>
                </BrowserRouter>
                <Spacer/>
                <StyledButton
                    onClick={handleSignOutClick}
                >Sign out</StyledButton>
            </ModalContent>
            <ModalActions>
                <StyledButton onClick={onDismiss}>Cancel</StyledButton>
            </ModalActions>
        </Modal>
    )
}

const StyledBalance = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`

const StyledBalanceWrapper = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  margin-bottom: ${(props) => props.theme.spacing[4]}px;
`

const StyledLink = styled(NavLink)`
  width: 100%;
  height: 40px;
  padding: 0 10px;
  font-weight: bold;
  font-size: 16px;
  background: none;
  color: white;
  border: 1px solid ${(props) => props.theme.color.green[800]};
  background-color: ${(props) => props.theme.color.green[800]}66;
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  box-sizing: border-box;

  &:hover {
    background-color: ${(props) => props.theme.color.green[800]}aa;
  }

  &:focus {
    outline: none;
  }
`

const StyledButton = styled.button`
  width: 100%;
  height: 40px;
  padding: 0 10px;
  font-weight: bold;
  font-size: 16px;
  background: none;
  color: white;
  border: 1px solid ${(props) => props.theme.color.green[800]};
  background-color: ${(props) => props.theme.color.green[800]}66;
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover {
    background-color: ${(props) => props.theme.color.green[800]}aa;
  }

  &:focus {
    outline: none;
  }
`
export default AccountModal
