import BigNumber from 'bignumber.js'
import React, {useCallback, useState} from 'react'
import styled from 'styled-components'
import {Contract} from 'web3-eth-contract'
import Card from '../../../components/Card'
import CardContent from '../../../components/CardContent'
import CardIcon from '../../../components/CardIcon'
import {AddIcon} from '../../../components/icons'
import Label from '../../../components/Label'
import Value from '../../../components/Value'
import useAllowance from '../../../hooks/useAllowance'
import useApprove from '../../../hooks/useApprove'
import useModal from '../../../hooks/useModal'
import useStake from '../../../hooks/useStake'
import useStakedBalance from '../../../hooks/useStakedBalance'
import useTokenBalance from '../../../hooks/useTokenBalance'
import useUnstake from '../../../hooks/useUnstake'
import {getBalanceNumber} from '../../../utils/formatBalance'
import DepositModal from './DepositModal'
import WithdrawModal from './WithdrawModal'
import details_logo from '../../../assets/img/details-logo.png'
import details1 from '../../../assets/img/details1.png'

interface StakeProps {
    lpContract: Contract
    contract: Contract
    tokenName: string
}

const Stake: React.FC<StakeProps> = ({lpContract, contract, tokenName}) => {
    const [requestedApproval, setRequestedApproval] = useState(false)
    const allowance = useAllowance(lpContract, contract)
    const {onApprove} = useApprove(lpContract, contract)
    // 
    const tokenBalance = useTokenBalance(lpContract.options.address)
    // 
    const stakedBalance = useStakedBalance(contract)
    const {onStake} = useStake(contract)
    const {onUnstake} = useUnstake(contract)

    const [onPresentDeposit] = useModal(
        <DepositModal
            max={tokenBalance}
            onConfirm={onStake}
            tokenName={tokenName}
        />,
    )

    const [onPresentWithdraw] = useModal(
        <WithdrawModal
            max={stakedBalance}
            onConfirm={onUnstake}
            tokenName={tokenName}
        />,
    )

    const handleApprove = useCallback(async () => {
        try {
            setRequestedApproval(true)
            const txHash = await onApprove()
            // user rejected tx or didn't go thru
            if (!txHash) {
                setRequestedApproval(false)
            }
        } catch (e) {
            console.log(e)
        }
    }, [onApprove, setRequestedApproval])

    return (
        <Card>
            <CardContent>
                <StyledCardContentInner>
                    <StyledCardHeader>
                        <CardIcon>
                            {
                                tokenName.toUpperCase() === 'NFT MASTER' &&
                                <img src={details1} alt=""/>
                            }
                            {
                                tokenName.toUpperCase() !== 'NFT MASTER' &&
                                <img src={details_logo} alt=""/>
                            }
                        </CardIcon>
                        <Value value={getBalanceNumber(stakedBalance)}/>
                        <Label text={`${tokenName} Staked`}/>
                    </StyledCardHeader>
                    <StyledCardActions>
                        {!allowance.toNumber() ? (
                            <StyledButton
                                disabled={requestedApproval}
                                onClick={handleApprove}
                            >
                                {`Approve ${tokenName}`}
                            </StyledButton>
                        ) : (
                            <>
                                <StyledButton
                                    disabled={stakedBalance.eq(new BigNumber(0))}
                                    onClick={onPresentWithdraw}
                                >
                                    Unstake
                                </StyledButton>
                                <StyledActionSpacer/>
                                <StyledButton onClick={onPresentDeposit} style={{width:'3rem'}}>
                                    <AddIcon/>
                                </StyledButton>
                            </>
                        )}
                    </StyledCardActions>
                </StyledCardContentInner>
            </CardContent>
        </Card>
    )
}

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;

  & > div:first-child {
    margin: 20px 0;
  }

  & > div:nth-child(2) {
    margin: 10px 0;
  }
`
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`

const StyledActionSpacer = styled.div`
  height: ${(props) => props.theme.spacing[4]}px;
  width: ${(props) => props.theme.spacing[4]}px;
`

const StyledCardContentInner = styled.div`
  align-items: center;
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: space-between;
`

const StyledButton = styled.button`
  height: 40px;
  width: 100%;
  padding: 0 10px;
  font-weight: bold;
  font-size: 14px;
  background: none;
  color: white;
  border: 1px solid white;
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover{
    border-color: ${(props) => props.theme.color.green[800]};
    color: ${(props) => props.theme.color.green[800]};
  }
  svg path:last-child{
    fill: #fff; 
  }
  &:hover svg path:last-child{
    fill: ${(props) => props.theme.color.green[800]}; 
  }

  &:focus {
    outline: none;
  }
`

export default Stake
