import React, {useEffect} from 'react'
import {BrowserRouter, Route, Switch, useRouteMatch} from 'react-router-dom'
import {useWallet} from 'use-wallet'

import Button from '../../components/Button'
import Page from '../../components/Page'
import PageHeader from '../../components/PageHeader'
import WalletProviderModal from '../../components/WalletProviderModal'

import useModal from '../../hooks/useModal'

import Farm from '../Farm'

import FarmCards from './components/FarmCards'
import styled from "styled-components"

import bg5 from '../../assets/img/bg5.png'
import logo_dark from '../../assets/img/details-logo.png'

const Farms: React.FC = () => {
    console.log('Frams page')
    const {path} = useRouteMatch()
    const {account, status, connect} = useWallet()

    useEffect(() => {
        if (status == 'disconnected') {
            connect('injected')
        }
    }, []);

    const [onPresentWalletProviderModal] = useModal(<WalletProviderModal/>)

    return (
        <Switch>
            <Page>
                <BrowserRouter>
                    {account ? (
                        <>
                            <Route exact path={path}>
                                <StyledWrapper>
                                    <PageHeader
                                        icon={<img src={logo_dark} height="120"/>}
                                        title="CHOOSE YOUR PTT POOL"
                                        subtitle="Earn PTT tokens by staking $PTT-HT LP Tokens."
                                    />
                                    <FarmCards/>
                                </StyledWrapper>
                            </Route>
                            <Route path={`${path}/:farmId`}>
                                <Farm/>
                            </Route>
                        </>
                    ) : (
                        <div
                            style={{
                                alignItems: 'center',
                                display: 'flex',
                                flex: 1,
                                justifyContent: 'center',
                            }}
                        >
                            <StyledButton
                                onClick={onPresentWalletProviderModal}
                            >
                                 Unlock Wallet
                            </StyledButton>
                        </div>
                    )}
                </BrowserRouter>
            </Page>
        </Switch>
    )
}

const StyledWrapper = styled.div`
  background-image: url('${bg5}');
  background-repeat: no-repeat;
  width: 100%;
  min-height: calc(100vh - 279px);
  margin-top: -1px;

  & > div:first-child {
    margin-top: 50px;
  }
`

const StyledButton = styled.button`
  width: 100%;
  padding: 10px 30px;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 16px;
  background: none;
  border:1px solid ${(props) => props.theme.color.green[800]};
  color: ${(props) => props.theme.color.green[800]};
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;

  &:hover {
    border-color: ${(props) => props.theme.color.green[700]};
    color: ${(props) => props.theme.color.green[700]};
  }

  &:focus {
    outline: none;
  }
`

export default Farms
