import React, {useCallback, useEffect, useState} from 'react'
import styled from 'styled-components'
import {useWallet} from 'use-wallet'
import useModal from '../../../hooks/useModal'
import WalletProviderModal from '../../WalletProviderModal'
import AccountModal from './AccountModal'
import {useTranslation} from "react-i18next";
import {useLocation} from "react-router-dom";

interface AccountButtonProps {
}

const AccountButton: React.FC<AccountButtonProps> = (props) => {
    const {t, i18n} = useTranslation()
    const {pathname, search} = useLocation()
    const langName = {
        en: 'English',
        zh: ''
    }

    let [openLanguage, setOpenLanguage] = useState(false)
    const [onPresentAccountModal] = useModal(<AccountModal/>)
    const [onPresentWalletProviderModal] = useModal(
        <WalletProviderModal/>,
        'provider',
    )

    const {account} = useWallet()

    const handleUnlockClick = useCallback(() => {
        onPresentWalletProviderModal()
    }, [onPresentWalletProviderModal])

    const toggleLang = e => {
        if (!e.target.classList.contains('lang-btn')) {
            setOpenLanguage(false)
        }
    }
    window.document.addEventListener('click', toggleLang, false)
    useEffect(() => {
        return () => {
            window.document.removeEventListener('click', toggleLang)
            setOpenLanguage(false)
        }
    }, [pathname])
    const changeLanguage = (lng: string) => {
        const oldLanguage = i18n.language;
        if (oldLanguage === lng) {
            return;
        }

        i18n.changeLanguage(lng, function () {
            if (Array.isArray(i18n.options.fallbackLng)) {
                let pathName = pathname === '/' ? '' : pathname
                pathName = pathName.replace(new RegExp(`\\/(${Object.keys(langName).join('|')})`, 'i'), '')
                let url = '';
                if (lng === i18n.options.fallbackLng[0]) {
                    url = (pathName || '/')
                } else {
                    url = '/' + lng + pathName
                }

                window.history.pushState({}, '', url)
            }
        })
    };

    return (
        <StyledAccountButton>
            {!account ? (
                <StyledButton onClick={handleUnlockClick}>
                    Unlock Wallet
                </StyledButton>
            ) : (
                <StyledButton onClick={onPresentAccountModal}>
                    My Wallet
                </StyledButton>
            )}
            <LanguageButton className="lang-btn" onClick={() => {
                setOpenLanguage(!openLanguage)
            }}>
                Switch language ({langName[i18n.language]})
                <LanguageList className={openLanguage ? 'showLang' : ''}>
                    {Object.keys(langName).map(item => <div
                        className={item === i18n.language ? 'active' : ''}
                        key={item}
                        onClick={() => {
                            changeLanguage(item)
                        }}
                    >
                        {langName[item]}
                    </div>)}
                </LanguageList>
            </LanguageButton>
        </StyledAccountButton>
    )
}

const StyledAccountButton = styled.div`
  display: flex;
  align-items: center;
`

const StyledButton = styled.button`
  width: auto;
  padding: 10px 15px;
  font-weight: bold;
  font-size: 16px;
  background: none;
  color: ${(props) => props.theme.color.primary.light};
  border: 1px solid ${(props) => props.theme.color.primary.light};
  border-radius: 0;
  box-shadow: none;
  cursor: pointer;

  &:hover {
    color: ${(props) => props.theme.color.white};
  }

  &:focus {
    outline: none;
  }
`
const LanguageButton = styled.div`
  user-select: none;
  margin-left: 1rem;
  color: white;
  height: 40px;
  display: flex;
  padding-left: 1rem;
  padding-right: 1rem;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  position: relative;
  box-shadow: inset 0 0 1px 1px ${(props) => props.theme.color.green[800]};
  background-color: ${(props) => props.theme.color.green[800]}66;
`

const LanguageList = styled.div`
  position: absolute;
  z-index: 1;
  top: calc(100% + 5px);
  right: 0;
  width: 100%;
  display: none;
  box-sizing: border-box;
  background-color: ${(props) => props.theme.color.green[800]};

  &.showLang {
    display: flex;
    flex-direction: column;
  }

  div {
    padding: 10px 1rem;

    &.active,
    &:hover {
      background-color: rgba(0, 0, 0, 0.1);
      color: ${(props) => props.theme.color.green[700]}
    }
  }
`

export default AccountButton
